<template>
  <div class="sub_module">
    <el-tabs v-model="questionTabSelected" @tab-click="GetStudentQuestion">
      <el-tab-pane v-for="(item, index) in questionTabOptions" :key="index" :label="item.label" :name="item.index">
        <el-row v-if="item.dataList && item.dataList.length>0" style="border:1px solid #f3f3f3;">
          <el-col :span="18">
            <!-- 提问记录 -->
            <div class="chat" ref="chat">
              <div class="chat_content">
                <div v-for="(sub, k) in item.dataList" :key="k"
                     :class=" sub['StudentID']!== selectStudentId ? 'chat_item' : 'chat_item checked_item'">
                  <div class="chat_news">

                    <div class="chat_news_name">
                      {{sub['StudentName']}}
                      <span>{{formatDate(sub['CreateTime'], 'hh:mm:ss')}}</span>
                    </div>
                    <div class="chat_news_cont">
                      <span>{{sub['QContent'] }}</span>
                    </div>
                  </div>
                </div>
              </div>

            </div>
          </el-col>
          <el-col :span="6">
            <div class="summary">提问人员</div>
            <div class="list_person">
              <div v-for="(item, index) in item.dataPerson" :key="index" class="person_item"
                   :class="selectStudentId === item['StudentID'] ? 'person_selected' : ''"
                   @click="selectStudentId=item['StudentID']">
                <img :src="defaultPhotoImg" alt=""/>
                <span>{{item['StudentName']}}</span>
                <span></span>
              </div>

            </div>
          </el-col>
        </el-row>
        <div v-else style="text-align: center;">
          暂无提问
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  import { formatDate } from "@/utils/utils";
  import imgPhoto from "./../../../../../../static/boy_img.png";

  export default {
    name: "StudentQuestion",
    props: {
      course: {
        type: Object
      }
    },
    data() {
      return {
        // course: JSON.parse(sessionStorage.getItem("course")),
        // courseID: sessionStorage.getItem("courseID"),
        dataList: [],
        studentQuestion: [],

        defaultPhotoImg: imgPhoto,
        selectStudentId: "",
        listLoading: false,

        courseState: {
          before: 0, // 课前
          at: 1, // 课中
          after: 2 // 课后
        },
        questionList: [],
        questionTabOptions: [],
        questionTabSelected: "",
      };
    },
    created() {
      // this.courseQuestion();
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      courseQuestion() {
        this.questionTabOptions = [];
        let state = this.course['State'];

        if (state === this.courseState.before) {
          this.questionTabOptions = [
            { index: "0", attribute: 1, label: "预习", disabled: false, dataList: [], dataPerson: [] }
          ];
        } else if (state === this.courseState.at) {
          this.questionTabOptions = [
            { index: "0", attribute: 1, label: "预习", disabled: true, dataList: [], dataPerson: [] },
            { index: "1", attribute: 2, label: "课中", disabled: false, dataList: [], dataPerson: [] }
          ]
        } else if (state === this.courseState.after) {
          this.questionTabOptions = [
            { index: "0", attribute: 1, label: "预习", disabled: true, dataList: [], dataPerson: [] },
            { index: "1", attribute: 2, label: "课中", disabled: true, dataList: [], dataPerson: [] },
            { index: "2", attribute: 3, label: "复习", disabled: false, dataList: [], dataPerson: [] }
          ]
        }
        this.questionTabSelected = this.questionTabOptions[state].index;
        this.GetStudentQuestion();
      },
      //获取学生提问
      GetStudentQuestion() {
        let dataList = [];
        let dataPerson = [];
        this.$http
          .get(this.$api.GetQuestionAllDatas, {
            courseID: this.course["CourseID"],
            classID: this.course["ClassID"],
            attribute: this.questionTabOptions[this.questionTabSelected].attribute
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                dataList.push(item);
                let index = dataPerson.findIndex(r => r.StudentID === item["StudentID"]);
                if (index > -1) {
                  dataPerson[index].Questions.push(item);
                } else {
                  dataPerson.push({
                    StudentID: item["StudentID"],
                    StudentName: item["StudentName"],
                    Questions: [item]
                  });
                }
              });
              this.questionTabOptions[this.questionTabSelected].dataList = dataList;
              this.questionTabOptions[this.questionTabSelected].dataPerson = dataPerson;
            }
          });
      },
    },
    computed: {},
    watch: {
      course(newVal, oldValue) {
        this.courseQuestion();
      }
    }
  }
</script>

<style lang="less" scoped>

  .chat {
    padding: 0 5px;
    text-align: left;
    width: 100%;
    max-height: 600px;
    overflow-y: auto;
    background: rgba(10, 10, 10, 0.1);
    border: 1px solid #f9f9f9;

    .chat_content {
      .chat_item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        .chat_news {
          .chat_news_name {
            font-size: 12px;
            line-height: 2.5;
            color: #409EFF;
          }

          .chat_news_cont {
            padding: 6px 5px 6px 18px;
            font-size: 14px;
            line-height: 1.5;

            text-align: left;
            word-break: break-all;
            /*border: 1px solid #777;*/
            /*border-radius: 7px;*/

            img {
              max-width: 45vw;
              max-height: 25rem;
            }
          }
        }
      }

      .checked_item {
        .chat_news {
          .chat_news_name {
            font-weight: bolder;
            color: #67C23A;
          }

          .chat_news_cont {
            color: #777;
          }
        }
      }
    }
  }

  .summary {
    text-align: center;
    font-size: 14px;
    color: rgba(153, 153, 153, 1);
    line-height: 1.5;
  }

  .list_person {
    max-height: 600px;
    overflow-y: auto;

    .person_item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px;
      line-height: 40px;

      &:hover {
        background-color: #ecf5ff;
      }

      img {
        max-width: 25px;
        max-height: 25px;
        border-radius: 50%;
      }
    }
  }

  .person_selected {
    background-color: #ecf5ff;
  }
</style>



