<template>
  <div id="course-view">
    <div class="main">
      <div class="top">
        <div class="tt">我的课程</div>

        <div class="top_operate">
          <el-date-picker
            v-model="dateRange"
            size="small"
            type="daterange"
            align="right"
            unlink-panels
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions"
            @change="selectDateRange"
            style="width: 300px;"></el-date-picker>
          <!-- <el-input placeholder="请输入内容" size="small" style="width:250px;">
            <el-button type="primary" slot="append" @click="searchData">搜索</el-button>
          </el-input> -->
          <el-button type="primary" size="small" @click="searchData">搜索</el-button>
          <el-button type="primary" size="small" @click="courseAdd">创建课程</el-button>
          <el-button type="primary" size="small" @click="studentCourseStats">学生课程统计</el-button>
          <el-button type="danger" size="small" :loading="deleteLoading" @click="deleteData">批量删除</el-button>
        </div>
      </div>

      <div class="filter-date">
        <div class="date">

          <el-button type="primary" @click="weekDataChange(-1)">上一周</el-button>
          <div
            class="item"
            v-for="(item, index) in weekData"
            :key="index"
            :class="weekIndex===index?'active':''"
            @click="selectDate(item, index)">
            <div class="info">
              <div class="day">{{weekName[index]}}</div>
              <div class="dt">{{item.date}}</div>
              <div class="nus">{{item.total}}</div>
            </div>
          </div>
          <el-button type="primary"  @click="weekDataChange(1)">下一周</el-button>
        </div>
      </div>

      <div class="content">
        <div class="table">
          <common-table
            :columns="tableColumns"
            :datas="tableDatas"
            th-class="table-innerhead"
            border
            :select="true"
            :pagger="false"
            :data-id="keyIdName"
            @selectHandler="selectChange">
            <template slot="ClassDate" slot-scope="scope">
              <span>{{ scope.row.ClassDate | formatDate(scope.row.ClassDate)}}</span>
            </template>
            <template slot="StartTime" slot-scope="scope">
              <span>{{ timeFormat(scope.row.StartTime) }} - {{ timeFormat(scope.row.EndTime) }}</span>
            </template>
            <template slot="State" slot-scope="scope">
              <span v-if="scope.row.State==0">未上课</span>
              <span v-if="scope.row.State==1">上课中</span>
              <span v-if="scope.row.State==2">已结束</span>
            </template>
            <template slot="oprate" slot-scope="scope">
              <el-button type="warning" plain size="mini" @click="coursePrepare(scope.row)">备课
              </el-button>
              <el-button type="primary" plain size="mini" @click="courseQuestion(scope.row)">学生提问
              </el-button>
              <el-button v-if="scope.row.State==0 && scope.row.IsSchedule==0" type="success" plain size="mini"
                         @click="courseEdit(scope.row)">编辑
              </el-button>
              <el-button v-if="scope.row.State!=0" type="primary" size="mini" @click="courseRecord(scope.row)"
                         style="background: #6acfce;border-color:#6acfce;">课堂记录
              </el-button>
            </template>
          </common-table>

          <div class="personal-bottom-pager">
            <el-pagination
              class="personnal-detail-wrap-pager"
              layout="prev, pager, next"
              :current-page.sync="formData.pno"
              :page-size="formData.psize"
              :total="dataTotal"
              prev-text="上一页"
              next-text="下一页"
              @current-change="pageChange"
            />
          </div>
        </div>
      </div>
    </div>

    <el-dialog title="学生课程" :visible.sync="dialogCourseVisible" style="text-align: left;">
      <common-table
        :columns="statsTC"
        :datas="courseDatas"
        th-class="table-innerhead"
        border
        v-loading="listLoading"
        :pagger="false">
        <template slot="oprate" align="center" slot-scope="scope">
          <el-button type="success" size="mini" @click="attendanceSatas(scope.row)">考勤</el-button>
        </template>
      </common-table>
    </el-dialog>

    <el-dialog title="学生考勤" :visible.sync="dialogAttendanceVisible" style="text-align: left;" :before-close="resetForm">
      <div class="attendance-search">
        <el-date-picker
          v-model="attendanceDateRange"
          size="small"
          type="daterange"
          align="right"
          unlink-panels
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions"
          style="width: 300px;">
        </el-date-picker>
        <el-button type="primary" size="small" @click="searchAttendance">搜索</el-button>
      </div>
      <common-table
        :columns="attendanceTC"
        :datas="attendanceDatas"
        th-class="table-innerhead"
        border
        v-loading="listLoading"
        :pagger="false">
      </common-table>
    </el-dialog>

    <el-dialog title="学生提问" :visible.sync="dialogQuestionVisible" style="text-align: left;">
      <student-question ref="studentQuestion" :course="questionCourse"></student-question>
    </el-dialog>

  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import StudentQuestion from "./../courseTeaching/StudentQuestion";
  import list from "@/mixin/list";
  import { getWeek, parseTime } from "@/utils/utils";

  export default {
    name: "",
    components: { commonTable, StudentQuestion },
    mixins: [list],
    props: {},
    data() {
      return {
        weekCount: 0,
        weekName: [
          "星期一",
          "星期二",
          "星期三",
          "星期四",
          "星期五",
          "星期六",
          "星期日"
        ],
        weekData: [],
        weekIndex: 0,
        dateRange: [],
        pickerOptions: {
          shortcuts: [
            {
              text: "最近一周",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "最近一个月",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit("pick", [start, end]);
              }
            },
            {
              text: "最近三个月",
              onClick(picker) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit("pick", [start, end]);
              }
            }
          ]
        },

        formData: {
          startDate: "",
          endDate: "",
          // key: "",
          thcid: this.$http.getThc().TeacherID,
          psize: 10,
          pno: 1
        },
        tableColumns: [
          {
            prop: "CourseName",
            label: "课程名称"
          },
          {
            prop: "ClassName",
            label: "班级"
          },
          {
            prop: "ClassroomName",
            label: "教室"
          },
          {
            prop: "ClassDate",
            label: "上课日期",
            slot: true,
            width: 110
          },
          {
            prop: "StartTime",
            label: "上课时间",
            slot: true,
            width: 120
          },
          {
            prop: "State",
            label: "状态",
            slot: true,
            width: 80
          },
          {
            prop: "oprate",
            label: "操作",
            slot: true,
            align: "right",
            width: 266
          }
        ],
        tableDatas: [],
        dataTotal: 0,
        keyIdName: "CourseID",
        selectedIds: [],
        deleteLoading: false,
        listLoading: false,
        dialogCourseVisible: false,
        courseDatas: [],
        statsTC: [
          {
            prop: "CourseName",
            label: "课程名称"
          },
          {
            prop: "ClassName",
            label: "班级名称"
          },
          {
            prop: "oprate",
            label: "操作",
            slot: true
          }],
        dialogAttendanceVisible: false,
        attendanceDatas: [],
        attendanceParas: [],
        attendanceDateRange: [],
        attendanceTC: [
          {
            prop: "StudentName",
            label: "姓名",
            sort: true
          },
          {
            prop: "Normal",
            label: "正常",
            sort: true
          },
          {
            prop: "Late",
            label: "迟到",
            sort: true
          },
          {
            prop: "Leave",
            label: "请假",
            sort: true
          },
          {
            prop: "Truancy",
            label: "旷课",
            sort: true
          }],

        questionCourse: {},
        dialogQuestionVisible: false,
      };
    },
    computed: {},
    watch: {},
    created() {
      this.total = this.tableDatas.length;
      this.init();
    },
    mounted() {
    },
    methods: {
      init() {
        this.weekIndex = new Date().getDay() - 1;
        this.weekIndex = this.weekIndex === -1 ? 6 : this.weekIndex;

        this.dateRange = [];
        this.weekDataChange(0);
      },

      weekDataChange(val) {
        this.weekCount += val;
        this.weekData = [];
        for (let i = 0; i < 7; i++) {
          this.weekData.push({ date: getWeek(i, this.weekCount), total: 0 });
        }
        this.getCourseStatistics();
        this.selectDate(this.weekData[this.weekIndex], this.weekIndex);
      },
      pageChange(page) {
        this.formData.pno = page;
        this.searchData();
      },
      selectDate(item, index) {
        this.weekIndex = index;
        this.formData.startDate = item.date;
        this.formData.endDate = item.date;
        this.searchData();
      },
      selectDateRange() {
        if (this.dateRange.length === 2) {
          this.formData.startDate = this.dateRange[0];
          this.formData.endDate = this.dateRange[1];
          this.weekIndex = -1;
        }
      },
      searchData() {
        this.tableDatas = [];
        this.$http
          .get(this.$api.GetScheduleDatas,
            {
              userID: this.$http.getUser().UserID,
              teacherID: this.$http.getThc().TeacherID,
              startTime: this.formData.startDate,
              endTime: this.formData.endDate
            })
          .then(res => {
            this.$http
              .get(this.$api.CourseList, this.formData)
              .then(res => {
                if (res.Result && res.Datas && res.Datas.length > 0) {
                  res.Datas.forEach(item => {
                    this.tableDatas.push(item);
                  });
                }
                if (res.Result) {
                  this.dataTotal = res.RowsCount || 0;
                }
              })
              .catch(err => {
              });
          })
          .catch(err => {
          });
      },
      getCourseStatistics() {
        this.$http
          .get(this.$api.CourseStats, {
            startDate: this.weekData[0].date,
            endDate: this.weekData[6].date,
            thcid: this.$http.getThc().TeacherID
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let dateValue = item["ClassDate"]
                  ? parseTime(new Date(item["ClassDate"]), "{y}-{m}-{d}")
                  : "";
                let index = this.weekData.findIndex(r => r.date === dateValue);
                this.weekData[index].total = item.Total;
              });
            }
          })
          .catch(err => {
          });
      },
      courseAdd() {
        this.$router.push("/main/teacherSpace/prepare/classTemplate");
      },
      deleteData() {
        if (this.selectedIds.length < 1) {
          this.$message({
            showClose: true,
            message: "请选择课程",
            type: "error"
          });
          return;
        }

        this.$confirm("此操作将删除课程, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.deleteLoading = true;
            this.$http
              .post(this.$api.CourseUpdateState, {
                ids: this.selectedIds.join(","),
                state: "-1"
              })
              .then(res => {
                if (res.Result) {
                  this.$message({
                    message: "删除成功！",
                    type: "success"
                  });

                  setTimeout(() => {
                    console.log(this.selectedIds.length);
                    this.weekData[this.weekIndex].total = this.weekData[this.weekIndex].total - this.selectedIds.length;

                    this.selectedIds = [];
                    this.searchData();
                  }, 1);
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.deleteLoading = false;
              })
              .catch(err => {
                this.$message({
                  showClose: true,
                  message: "删除失败",
                  type: "error"
                });
                this.deleteLoading = false;
              });
          })
          .catch(() => {
          });
      },
      selectChange(selected) {
        this.selectedIds = selected;
      },

      courseEdit(row) {
        this.$router.push({
          path: "/main/teacherSpace/prepare/createCourse",
          query: {
            courseId: row["CourseID"]
          }
        });
      },
      coursePrepare(row) {
        //课程信息缓存在sessionStorage，关闭页面自动清除
        sessionStorage.setItem("course", JSON.stringify(row));
        sessionStorage.setItem("courseID", row["CourseID"]);
        this.$router.push("/main/teacherSpace/prepare/lessonInfo");
      },
      courseRecord(row) {
        //课程信息缓存在sessionStorage，关闭页面自动清除
        sessionStorage.setItem("course", JSON.stringify(row));
        sessionStorage.setItem("courseID", row["CourseID"]);
        this.$router.push("/main/teacherSpace/prepare/classRecord");
      },
      studentCourseStats() {
        this.dialogCourseVisible = true;
        this.courseDatas = [];

        this.$http
          .get(this.$api.GetDatasByTeacherID, { teacherID: this.$http.getThc().TeacherID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach((item, index) => {
                this.courseDatas.push({
                  CourseID: item.CourseID,
                  CourseName: item.CourseName,
                  ClassID: item.ClassID,
                  ClassName: item.ClassName
                });
              });
            }
            this.listLoading = false;
          })
          .catch(err => {
            this.listLoading = false;
          });
      },
      searchAttendance() {
        this.attendanceDatas = [];
        this.$http
          .get(this.$api.GetSignStatsByDate, {
            startDate: this.attendanceDateRange[0],
            endDate: this.attendanceDateRange[1],
            courseName: this.attendanceParas[0].CourseName,
            classID: this.attendanceParas[0].ClassID,
            className: this.attendanceParas[0].ClassName
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach((item, index) => {
                this.attendanceDatas.push({
                  StudentID: item.StudentID,
                  StudentName: item.StudentName,
                  Normal: item.Normal,
                  Late: item.Late,
                  Leave: item.Leave,
                  Truancy: item.Truancy
                });
              });
            }
            this.listLoading = false;
          })
          .catch(err => {
            this.listLoading = false;
          });
      },

      attendanceSatas(row) {
        this.attendanceDatas = [];
        this.attendanceParas = [];
        this.dialogAttendanceVisible = true;

        this.attendanceParas.push({
          CourseName: row.CourseName,
          ClassID: row.ClassID,
          ClassName: row.ClassName
        });
      },

      resetForm() {
        this.attendanceDateRange = [];
        this.dialogAttendanceVisible = false;
      },

      courseQuestion(row) {
        this.questionCourse = row;
        this.dialogQuestionVisible = true;
        setTimeout(() => {
          this.$refs["studentQuestion"].courseQuestion();
        }, 100);
      },

      timeFormat(val) {
        if (val.length < 8)
          return val;

        return val.substring(0, val.length - 3);
      }
    }
  };
</script>

<style lang='less' scoped>
  #course-view {
    width: 956px;

    .main {
      height: 100%;
      background: #fff;
      padding: 20px;

      .ketajlu {
        background: #6acfce;
      }

      .top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;

        .tt {
          font-size: 14px;
          font-family: MicrosoftYaHei;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          margin-top: 5px;
        }

        .top_operate {
          float: right;
        }
      }

      .filter-date {
        margin-bottom: 15px;
        display: flex;
        justify-content: space-between;

        .date {
          width: 100%;
          height: 50px;
          // border: 1px solid #000;
          background: #ecf1f7;
          display: flex;
          justify-content: space-between;

          .active {
            background: #2a9efc !important;

            .info {
              .day,
              .dt {
                color: #fff !important;
                background: #2a9efc !important;
              }

              .nus {
                border: 1px solid #fff;
              }
            }
          }

          .item {
            position: relative;
            width: 14.28%;
            display: flex;
            align-items: center;
            justify-content: center;

            .info {
              .day {
                font-size: 16px;
                color: #333;
              }

              .dt {
                font-size: 12px;
                color: #666;
                line-height: 22px;
              }
            }
          }

          .nus {
            position: absolute;
            top: 5px;
            right: 5px;
            width: 24px;
            height: 12px;
            background: rgba(42, 158, 252, 1);
            border-radius: 6px;
            font-size: 10px;
            color: #fff;
            line-height: 12px;
          }
        }
      }

      .personal-bottom-pager {
        text-align: right;
        user-select: none;
        margin-top: 25px;

        .btn-next,
        .btn-prev,
        .el-pager li {
          border: 1px solid #dfdfdf;
          color: #666;
          font-weight: 400;
        }

        .el-pager li {
          border-left: none;
        }

        .el-pager li.active {
          background: #409eff;
          color: #fff;
          border-color: #409eff;
        }

        .btn-next,
        .btn-prev {
          padding: 0 5px;
        }

        .btn-prev {
          border-top-left-radius: 3px;
          border-bottom-left-radius: 3px;
        }

        .btn-next {
          border-top-right-radius: 3px;
          border-bottom-right-radius: 3px;
          border-left: none;
        }
      }
    }

    .attendance-search {
      margin-bottom: 10px;
    }
  }

  /deep/ .el-range-separator {
    width: 35px;
  }
</style>



